<template>
    <el-form :model="getLoginAccount" :rules="rules" ref="ruleForm2" label-position="left" label-width="0px"
             class="demo-ruleForm login-container">
      <h3 class="title">壹达汽车服务管理平台</h3>
      <el-form-item prop="username">
        <el-input type="text" v-model="getLoginAccount.username" auto-complete="off" placeholder="手机号"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="getLoginAccount.password" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-checkbox v-model="getLoginAccount.isRememberMe" checked class="remember">记住密码</el-checkbox>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit" :loading="isLogin">登录
      </el-button>
        <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
      </el-form-item>
    </el-form>
</template>
<script>
  export default {
    data() {
      return {
        rules: {
          username: [
            {required: true, message: '请输入账号', trigger: 'blur'},
          ],
          password: [
            {required: true, message: '请输入密码', trigger: 'blur'},
          ]
        },
      };
    },

    methods: {
      handleSubmit() {
        let data = {
          username: this.getLoginAccount.username,
          password: this.getLoginAccount.password,
          isRememberMe: this.getLoginAccount.isRememberMe,
        }
        this.$store.dispatch('login', data)
      },
    },

    computed: {
      sysUser(){
        return this.$store.getters.getCurUser;
      },

      getLoginAccount(){
        return this.$store.getters.getLoginAccount;
      },

      isLogin(){
        return this.$store.getters.isLogining
      },
      isAutoLogin(){
          return this.$store.getters.isAutoLogin
      }
    },

  }
</script>

<style lang="scss" scoped>
  .login-container {
    /* box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 100px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  .remember {
    margin: 0px 0px 35px 0px;
  }
</style>



